.svg-container,
.text-grid,
.main-title {
  opacity: 0;
  pointer-events: none;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  pointer-events: none;
}
.color-switcher {
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 0.5rem;
  pointer-events: auto;
}
.color-btn {
  width: 12px;
  height: 12px;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  transition: transform 0.2s ease;
}
.color-btn:hover {
  transform: scale(1.2);
}
.color-btn.active {
  transform: scale(1.3);
}
.theme-original {
  background: linear-gradient(135deg, #340b05 0%, #0358f7 25%, #ffd400 50%, #fa3d1d 75%, #fd02f5 100%);
}
.theme-blue-pink {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 25%, #ffffff 50%, #f472b6 75%, #ec4899 100%);
}
.theme-blue-orange {
  background: linear-gradient(135deg, #1e40af 0%, #3b82f6 25%, #ffffff 50%, #fb923c 75%, #ea580c 100%);
}
.theme-sunset {
  background: linear-gradient(135deg, #fef3c7 0%, #fcd34d 25%, #f59e0b 50%, #dc2626 75%, #7f1d1d 100%);
}
.theme-purple {
  background: linear-gradient(135deg, #f3e8ff 0%, #c084fc 25%, #8b5cf6 50%, #7c3aed 75%, #5b21b6 100%);
}
.theme-monochrome {
  background: linear-gradient(135deg, #1a1a1a 0%, #404040 25%, #999999 50%, #e5e5e5 75%, #ffffff 100%);
}
.theme-pink-purple {
  background: linear-gradient(135deg, #fdf2f8 0%, #fce7f3 25%, #f9a8d4 50%, #f472b6 75%, #ec4899 100%);
}
.theme-blue-black {
  background: linear-gradient(135deg, #000000 0%, #1e293b 25%, #334155 50%, #475569 75%, #64748b 100%);
}
.theme-beige-black {
  background: linear-gradient(135deg, #fef3c7 0%, #f59e0b 25%, #d97706 50%, #92400e 75%, #451a03 100%);
}
.blur-toggle {
  position: absolute;
  top: 4.5rem;
  left: calc(50% - 60px);
  transform: translateX(-50%);
  pointer-events: auto;
  padding: 8px 20px;
}
.randomize-toggle {
  position: absolute;
  top: 4.5rem;
  left: calc(50% + 60px);
  transform: translateX(-50%);
  pointer-events: auto;
  padding: 8px 20px;
}
.blur-btn,
.randomize-btn {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--color-text);
  cursor: pointer;
  text-decoration: none;
  position: relative;
  display: inline-block;
  transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1), color 0.3s ease;
  user-select: none;
}
.blur-btn::before,
.randomize-btn::before {
  content: "â€¢";
  position: absolute;
  left: -12px;
  top: 0;
  opacity: 0;
  color: var(--color-text-light);
  transition: opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1), color 0.3s ease;
}
.blur-toggle:hover .blur-btn,
.randomize-toggle:hover .randomize-btn {
  transform: translateX(8px);
}
