  0% {
    d: path("M2 6 Q4 2 6 6 Q8 10 10 6 Q12 2 14 6 Q16 10 18 6");
  }
  50% {
    d: path("M2 6 Q4 10 6 6 Q8 2 10 6 Q12 10 14 6 Q16 2 18 6");
  }
  100% {
    d: path("M2 6 Q4 2 6 6 Q8 10 10 6 Q12 2 14 6 Q16 10 18 6");
  }
