.hero-text-content {
  display: flex;
  gap: 2rem;
  flex: 1;
  justify-content: flex-end;
}
.hero-text {
  width: 25%;
  font-family: var(--font-mono);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--color-text);
  line-height: 1.6;
  overflow: hidden;
  transition: color 0.3s ease;
}
.hero-title .char,
.hero-title div,
.hero-nav-item div,
.hero-text div,
.wavelength-label div,
.main-title div,
.nav-bottom-center .char {
  will-change: transform, opacity, filter, color;
}
@media (max-width: 768px) {
  .hero-section {
    padding: 1rem;
  }
  .hero-title {
    font-size: 12vw;
    margin-top: 2rem;
  }
  .hero-content {
    flex-direction: column;
    gap: 1.5rem;
    margin-top: 2rem;
  }
  .hero-text-content {
    flex-direction: column;
    gap: 1.5rem;
  }
  .hero-text {
    width: 100%;
    font-size: 10px;
  }
  .color-switcher {
    gap: 0.3rem;
    top: 1rem;
  }
  .blur-toggle {
    top: 3rem;
    left: calc(50% - 30px);
  }
  .randomize-toggle {
    top: 3rem;
    left: calc(50% + 30px);
  }
  .color-btn {
    width: 10px;
    height: 10px;
  }
  .text-grid {
    gap: 0.2rem;
  }
  .wavelength-label {
    font-size: 9px;
    line-height: 1.2;
  }
  .nav-item {
    font-size: 10px;
  }
  .nav-top-left,
	.nav-top-right {
    top: 3rem;
  }
  .nav-top-left {
    left: 1rem;
  }
  .nav-top-right {
    right: 1rem;
    gap: 2px;
  }
  .nav-top-right,
	.nav-bottom-right {
    right: 1rem;
  }
  .nav-bottom-left,
	.nav-bottom-right,
	.nav-bottom-center {
    bottom: 1rem;
  }
  .sound-wave {
    width: 18px;
    height: 10px;
  }
}
noscript .hero-title,
noscript .hero-nav-item,
noscript .hero-text,
noscript .nav-bottom-center,
noscript .svg-container,
noscript .text-grid,
noscript .main-title,
noscript .wavelength-label {
  visibility: visible !important;
}
